.slider-inner {
  background: url("../../media/hero-img.jpg") center center no-repeat;
  background-size: cover;
  color: white;
  min-height: 780px;
  display: flex;
  align-items: center;
}
.slider-inner:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(270deg, rgba(41, 47, 66, 0) 0%, rgba(41, 47, 66, 0.2) 32.29%, rgba(41, 49, 66, 0.55) 100%);
  z-index: 1;
}
@media (max-width: 768px) {
  .slider-inner {
    min-height: 500px;
  }
}
@media (max-width: 420px) {
  .slider-inner {
    min-height: 300px;
  }
}
.slider-inner .container {
  z-index: 2;
}
.slider-inner__content h1 {
  font-size: 92px;
  font-weight: 900;
}
@media (max-width: 768px) {
  .slider-inner__content h1 {
    font-size: 70px;
  }
}
@media (max-width: 420px) {
  .slider-inner__content h1 {
    font-size: 30px;
  }
}

.my-carousel-progress {
  background: #ccc;
}

.my-carousel-progress-bar {
  background: #f50014;
  height: 8px;
  transition: width 400ms ease;
  width: 0;
}

.influencer {
  padding: 0 0 0 120px;
  border-left: 1px solid #2b2b2a;
  max-width: 50%;
  margin: 220px auto;
}
@media (max-width: 768px) {
  .influencer {
    padding: 0 0 0 80px;
    max-width: 70%;
    margin: 120px auto;
  }
}
@media (max-width: 420px) {
  .influencer {
    padding: 0 0 0 50px;
    max-width: 90%;
    margin: 70px auto;
  }
}
.influencer img {
  max-width: 100%;
  transition: all ease-in 0.2s;
}
.influencer img:hover {
  transform: scale(1.1);
}
.influencer h2 {
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 20px;
}
@media (max-width: 420px) {
  .influencer h2 {
    font-size: 30px;
  }
}
.influencer p {
  margin-top: 40px;
}
.influencer .insta-handle {
  display: inline-block;
  margin-bottom: 20px;
}
.influencer .insta-handle:hover {
  color: #c20010;
}
.influencer .insta-handle > span {
  display: flex;
  align-items: center;
}
.influencer .insta-handle i {
  margin-right: 10px;
  font-size: 18px;
}

.social-cards {
  background: url("../../media/cross-texture.png") center center no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.social-card {
  background: #fff;
  padding: 25px;
  box-shadow: 0px 8.81696px 14.9888px rgba(0, 0, 0, 0.0976289);
  max-width: 355px;
  margin: 40px auto;
}
.social-card--lg {
  max-width: 395px;
  margin: 40px auto;
}
.social-card--sm {
  max-width: 288px;
  padding: 20px;
  margin: 20px auto;
}
.social-card button {
  border: none;
  background: none;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  transition: all ease-in-out 0.2s;
}
.social-card button:hover {
  background: #f2f2f2;
}
.social-card__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.social-card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}
.social-card__footer .social-card-footer__left {
  display: flex;
  align-items: center;
}
.social-card__footer button:focus.heart-icon svg {
  fill: #f50014;
}
.social-card .user {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social-card .user img {
  margin-right: 10px;
}
.social-card .user__avatar {
  display: flex;
  align-items: center;
}
.social-card .user__name {
  font-weight: bold;
  font-size: 14px;
}
.social-card .user__location {
  font-size: 11px;
  font-weight: bold;
}

.why-you-should-go {
  margin: 120px 0;
}
.why-you-should-go__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 420px) {
  .why-you-should-go__footer {
    flex-direction: column;
  }
}
.why-you-should-go .share {
  display: flex;
  align-items: center;
}
@media (max-width: 420px) {
  .why-you-should-go .share {
    margin-top: 30px;
  }
}
.why-you-should-go .share span {
  font-weight: bold;
  font-size: 18px;
}
.why-you-should-go .share a {
  display: flex;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  border: 2px solid #2b2b2a;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all ease-in-out 0.2s;
}
.why-you-should-go .share a i {
  font-size: 36px;
  color: #2b2b2a;
}
.why-you-should-go .share a:hover {
  background: #f50014;
  border: 2px solid #fff;
}
.why-you-should-go .share a:hover i {
  font-size: 36px;
  color: #fff;
}
.why-you-should-go h2 {
  font-weight: 900;
  font-size: 70px;
  color: #f50014;
}
.why-you-should-go p {
  margin-top: 30px;
  font-size: 14px;
}

.socials {
  background: #fff;
  padding: 40px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  position: relative;
  z-index: 2;
  font-weight: bold;
}
@media (max-width: 420px) {
  .socials {
    flex-direction: column;
    font-size: 18px;
  }
}
.socials__link {
  display: flex;
}
.socials__link a {
  color: #cbcbcb;
  margin: 0 10px;
  display: flex;
}
.socials__link a:hover {
  color: #f50014;
}